.homepage{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 500px;
    height: 500px;
    border-radius: 20px;
    box-shadow: 0 0 15px 5px grey;
    background: rgb(255, 255, 255);
    position: relative;
}

.button {
background: rgba(49, 73, 232, 0.858);
border:  1px solid blue;
color: white;
font-size: medium;
padding: 0.5rem;
border-radius: 8px;
outline: none;
cursor: pointer;
margin-top:50px ;
}
