.login {
    width: 400px;
    background: rgb(255, 255, 255);
    border: 1px solid rgb(94, 10, 10);
    box-shadow: 0 15px 15px grey;
    border-radius: 8px;
    padding: 5rem;
    align-items: center;
    text-align: center;  
    height: 400px;
    
}

.login input{
    border-radius: 8px;
    border: 2px solid rgb(187, 186, 186);
    height: 40px;
    color: rgb(159, 157, 157);
    margin-top: 15px;
    padding-top: 5px ;
    width: 100%;
    font-size: medium;

}

.or {
     
    margin-top: 15px;
    text-align: center;
    padding-top: 10px;
    font-size: 28px;


}
  .button{
    background-color: rgb(71, 118, 249);
    border: 1px solid blue;
    color: aliceblue;
    font-size: 1.23 rem;
    padding: 0.5rem;
    margin-top: 20px;
    border-radius: 8px;
    outline:none;
    cursor: pointer;
 }